.footer {
  margin-top: auto;
  padding-top: 50px;
  padding-bottom: 55px;
  font-size: 16px;
  line-height: 20px;
  color: $color-white-trasparent;
  background-color: $color-black-pearl;

  @include vp-1023 {
    padding-bottom: 65px;
  }

  @include vp-767 {
    padding-top: 32px;
    padding-bottom: 35px;
  }
}

.footer__container {
  display: grid;
  grid-template-columns: 1fr 38.52% 28.29%;
  gap: 30px;

  @include vp-1023 {
    grid-template-columns: 1fr;
    gap: 35px 110px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    gap: 25px;
  }
}

.footer__logo-wrapper {
  display: flex;
  flex-direction: column;

  @include vp-1023 {
    flex-direction: row;
    justify-content: space-between;
    grid-column: 1/-1;
  }

  @include vp-767 {
    flex-direction: column;
    align-items: center;
  }
}

.footer__logo {
  display: block;
  width: 215px;
  margin-bottom: 30px;

  @include vp-1023 {
    margin-bottom: 0;
  }
}

.footer__social-list {
  margin: 0;
  margin-left: 10px;

  @include vp-1023 {
    margin-top: 5px;
  }

  @include vp-767 {
    margin-top: 15px;
  }
}

.footer__accordion {
  display: flex;
  justify-content: space-between;

  @include vp-767 {
    flex-direction: column;
    justify-content: flex-start;
  }
}

.footer__column {
  display: flex;
  flex-direction: column;
  align-self: stretch;
  width: 32.8358%;

  @include vp-767 {
    width: auto;
    margin-left: -100px;
    margin-right: -100px;
    padding: 5px 100px;
    border: 1px solid $color-white-trasparent;
  }
}

.footer__column + .footer__column {
  width: 47.7612%;

  @include vp-767 {
    width: auto;
    margin-top: -1px;
    border-top: none;
  }
}

.footer__title {
  margin-top: 5px;
  margin-bottom: 20px;
  font-size: 15px;
  line-height: 20px;
  letter-spacing: 0.05em;
  color: $color-default-white;
  text-transform: uppercase;

  @include vp-1023 {
    margin-top: 0;
  }

  @include vp-767 {
    margin-bottom: 0;
    padding: 15px 0;
  }
}

.footer__title {
  position: relative;

  @include vp-767 {

    &::before,
    &::after {
      position: absolute;
      top: 22px;
      right: 0;
      width: 15px;
      height: 1px;
      background-color: $color-white-trasparent;
      content: ""
    }

    &::after {
      display: block;
      transform: rotate(90deg);
      transition: opacity $trans-default;
    }
  }
}

.is-active .footer__title {

  @include vp-767 {

    &::after {
      opacity: 0;
    }
  }
}

.footer-list__item {
  margin-top: 10px;

  &:first-child {
    margin-top: 0;
  }

  @include vp-767 {


    &:last-child {
      margin-bottom: 15px;
    }
  }
}

.footer__address .footer-list__item:first-child {
  font-style: normal;

  @include vp-767 {
    margin-bottom: 15px;
  }
}

.footer__link {
  display: block;
  font-style: normal;
  color: inherit;
  transition: opacity $trans-default;

  @include hover {
    opacity: 0.6;
  }

  @include focus {
    outline: 3px solid $color-deep-sky-blue;
  }

  @include active {
    opacity: 0.2;
  }

  &:disabled,
  &.disabled {
    opacity: 0.6;
    pointer-events: none;
  }
}
